@import "@styles/_mixins.scss";

.container {
  @include container;
  display: flex;
  flex-direction: column;
  background-color: var(--main-background);
  padding-top: 40px;

  .logoWrapper {
    position: relative;
    width: 170px;
    height: 170px;
    margin: 0 auto;

    &::before {
      position: absolute;
      left: 0;
      top: 0;
      content: "";
      width: 180px;
      height: 180px;
      border: 15px solid var(--title);
      border-radius: 15%;
    }

    & span {
      position: relative;
      display: block;
      width: 180px;
      height: 180px;
      font-size: 60px;
      font-weight: 700;
      color: var(--title);
      text-align: center;

      &::before {
        position: absolute;
        top: 20px;
        left: 0px;
        content: "";
        width: 10px;
        height: 10px;
        background-color: white;
        border-radius: 50%;
        animation: moveX 2s linear infinite alternate,
          moveY 2.5s linear infinite alternate;
        z-index: 2;
      }

      &::after {
        position: absolute;
        top: 20px;
        left: 0px;
        content: "";
        width: 10px;
        height: 10px;
        background-color: var(--pink-tab);
        border-radius: 50%;
        animation: moveX2 2s linear infinite alternate,
          moveY2 2.5s linear infinite alternate;
        z-index: 2;
      }
    }
  }

  .inputWrapper {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 300px;
    margin: 0 auto;
    padding-top: 50px;
    text-align: center;

    .linkto {
      width: 200px;
      color: var(--title);
      font-size: 20px;
      font-weight: 700;
      border: 3px solid var(--title);
      border-radius: 10px;
      transition: transform 0.2 ease;

      &:hover {
        transform: scale(0.9);
      }
    }
  }
}

@keyframes moveX {
  from {
    left: 15px;
  }
  to {
    left: calc(160px - 5px);
  }
}

@keyframes moveY {
  from {
    top: 15px;
  }
  to {
    top: calc(160px - 5px);
  }
}

@keyframes moveX2 {
  from {
    left: calc(160px - 5px);
  }
  to {
    left: 15px;
  }
}

@keyframes moveY2 {
  from {
    top: calc(160px - 5px);
  }
  to {
    top: 15px;
  }
}
